<template>
  <div class="taiwan-map-container">
    <svg
      v-html="svgContent"
      class="taiwan-map"
      @click="handleClick"
    />

    <!-- 景點彈窗 -->
    <div
      v-if="selectedSite"
      class="popup-mask"
      @click.self="selectedSite = null"
    >
      <div class="popup">
        <h3>{{ selectedSite.name }}</h3>
        <p>{{ selectedSite.category }} - {{ selectedSite.county }}</p>
        <button @click="selectedSite = null">關閉</button>
      </div>
    </div>

    <!-- 清單：點縣市後顯示 -->
    <ul v-if="filteredSites.length">
      <li v-for="site in filteredSites" :key="site.id" @click="play(site.url)">
        {{ site.name }}
      </li>
    </ul>
  </div>
</template>

<script>
const sites = [
  { id: 1, name: "多良車站", county: "台東縣", category: "觀光", url: "https://example.com/duoliang.mp4" },
  { id: 2, name: "台北101", county: "台北市", category: "觀光", url: "https://example.com/taipei101.mp4" },
  { id: 3, name: "高雄愛河", county: "高雄市", category: "觀光", url: "https://example.com/love-river.mp4" },
  { id: 4, name: "太魯閣", county: "花蓮縣", category: "觀光", url: "https://example.com/taroko.mp4" },
  // 之後可加更多地點
];

const svgContent = `<svg viewBox="0 0 800 1000" xmlns="http://www.w3.org/2000/svg">
  <!-- 台灣本島輪廓 -->
  <path d="M400 200 
           C420 180, 450 190, 480 210
           C500 230, 520 250, 540 280
           C550 320, 560 360, 570 400
           C580 450, 590 500, 600 550
           C610 600, 620 650, 630 700
           C640 750, 650 800, 640 850
           C620 880, 580 900, 540 920
           C500 940, 460 950, 420 960
           C380 950, 340 920, 300 900
           C260 880, 240 840, 220 800
           C210 750, 200 700, 190 650
           C180 600, 170 550, 160 500
           C150 450, 140 400, 130 350
           C120 300, 110 250, 100 200
           C90 150, 100 100, 200 120
           C300 140, 400 200, 400 200 Z" 
        fill="#f5f5f5" 
        stroke="#333" 
        stroke-width="2"/>
  
  <!-- 台北市 -->
  <path d="M380 280 
           C400 270, 420 280, 430 300
           C425 320, 410 330, 390 340
           C375 330, 370 310, 365 290
           C370 285, 380 280, 380 280 Z" 
        data-name="台北市"
        fill="#74b9ff" 
        stroke="#fff" 
        stroke-width="1.5"
        class="county-path"/>
  
  <!-- 新北市 -->
  <path d="M360 270 
           C380 260, 400 270, 420 280
           C440 290, 450 310, 460 340
           C450 360, 430 380, 410 400
           C380 390, 350 370, 320 350
           C310 320, 330 290, 345 280
           C352 275, 360 270, 360 270 Z" 
        data-name="新北市"
        fill="#a29bfe" 
        stroke="#fff" 
        stroke-width="1.5"
        class="county-path"/>
  
  <!-- 基隆市 -->
  <path d="M470 260 
           C480 255, 490 260, 495 270
           C490 280, 480 285, 470 280
           C465 275, 470 260, 470 260 Z" 
        data-name="基隆市"
        fill="#55a3ff" 
        stroke="#fff" 
        stroke-width="1.5"
        class="county-path"/>
  
  <!-- 桃園市 -->
  <path d="M320 250 
           C340 240, 360 250, 380 260
           C390 270, 400 280, 410 300
           C400 310, 380 320, 360 330
           C340 320, 320 300, 310 280
           C315 265, 320 250, 320 250 Z" 
        data-name="桃園市"
        fill="#00b894" 
        stroke="#fff" 
        stroke-width="1.5"
        class="county-path"/>
  
  <!-- 新竹市 -->
  <path d="M300 320 
           C315 315, 330 320, 340 325
           C335 335, 325 340, 310 345
           C305 340, 300 335, 295 330
           C297 325, 300 320, 300 320 Z" 
        data-name="新竹市"
        fill="#00cec9" 
        stroke="#fff" 
        stroke-width="1.5"
        class="county-path"/>
  
  <!-- 台中市 -->
  <path d="M280 420 
           C300 410, 320 420, 340 430
           C350 450, 340 480, 320 500
           C300 490, 280 470, 260 450
           C250 430, 270 420, 280 420 Z" 
        data-name="台中市"
        fill="#6c5ce7" 
        stroke="#fff" 
        stroke-width="1.5"
        class="county-path"/>
  
  <!-- 高雄市 -->
  <path d="M240 680 
           C260 670, 280 680, 300 690
           C310 710, 300 730, 280 750
           C260 740, 240 720, 220 700
           C210 690, 230 680, 240 680 Z" 
        data-name="高雄市"
        fill="#fd79a8" 
        stroke="#fff" 
        stroke-width="1.5"
        class="county-path"/>
  
  <!-- 台東縣 -->
  <path d="M520 700 
           C540 690, 560 700, 580 710
           C590 730, 580 750, 560 770
           C540 760, 520 740, 500 720
           C490 710, 510 700, 520 700 Z" 
        data-name="台東縣"
        fill="#fdcb6e" 
        stroke="#fff" 
        stroke-width="1.5"
        class="county-path"/>
  
  <!-- 花蓮縣 -->
  <path d="M480 600 
           C500 590, 520 600, 540 610
           C550 630, 540 660, 520 680
           C500 670, 480 650, 460 630
           C450 620, 470 600, 480 600 Z" 
        data-name="花蓮縣"
        fill="#74b9ff" 
        stroke="#fff" 
        stroke-width="1.5"
        class="county-path"/>
  
  <!-- 屏東縣 -->
  <path d="M260 720 
           C280 710, 300 720, 320 730
           C330 750, 320 780, 300 800
           C280 790, 260 770, 240 750
           C230 740, 250 720, 260 720 Z" 
        data-name="屏東縣"
        fill="#fab1a0" 
        stroke="#fff" 
        stroke-width="1.5"
        class="county-path"/>
  
  <!-- 景點：多良車站 -->
  <circle cx="540" cy="740" r="8" 
          data-name="多良車站" 
          fill="#e17055" 
          stroke="#fff" 
          stroke-width="2"
          class="site-marker"/>
  
  <!-- 景點：台北101 -->
  <circle cx="400" cy="310" r="6" 
          data-name="台北101" 
          fill="#00cec9" 
          stroke="#fff" 
          stroke-width="2"
          class="site-marker"/>
  
  <!-- 景點：高雄愛河 -->
  <circle cx="260" cy="710" r="6" 
          data-name="高雄愛河" 
          fill="#e84393" 
          stroke="#fff" 
          stroke-width="2"
          class="site-marker"/>
          
  <!-- 景點：太魯閣 -->
  <circle cx="490" cy="630" r="6" 
          data-name="太魯閣" 
          fill="#00b894" 
          stroke="#fff" 
          stroke-width="2"
          class="site-marker"/>
</svg>`;

export default {
  name: 'TaiwanMap',
  data() {
    return {
      selectedCounty: null,
      selectedSite: null,
      svgContent: svgContent
    };
  },
  computed: {
    filteredSites() {
      if (!this.selectedCounty) return [];
      return sites.filter(site => site.county === this.selectedCounty);
    }
  },
  methods: {
    handleClick(e) {
      const target = e.target;
      if (target.tagName === "path") {
        // 選縣市
        this.selectedCounty = target.dataset.name;
      } else if (target.tagName === "circle") {
        // 選景點
        const name = target.dataset.name;
        this.selectedSite = sites.find(site => site.name === name);
      }
    },
    play(url) {
      console.log("播放影片:", url);
      // 連到你現有播放器
    }
  }
};
</script>

<style>
.taiwan-map-container { 
  position: relative; 
  width: 400px; 
  max-width: 100%;
  padding: 20px;
}

.taiwan-map { 
  width: 100%; 
  height: auto; 
  cursor: pointer; 
  border: 1px solid #ddd;
  border-radius: 8px;
}

.taiwan-map path:hover {
  fill: #0984e3 !important;
  opacity: 0.8;
}

.taiwan-map circle:hover {
  fill: #d63031 !important;
  stroke-width: 3;
}

ul { 
  list-style: none; 
  padding: 0; 
  margin-top: 20px;
}

li { 
  cursor: pointer; 
  padding: 8px 12px; 
  border-bottom: 1px solid #ccc; 
  background: #f8f9fa;
  transition: background 0.2s;
}

li:hover { 
  background: #e9ecef; 
}

/* Popup styles */
.popup-mask {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.popup {
  background: #fff;
  padding: 20px;
  border-radius: 12px;
  width: 280px;
  text-align: center;
  box-shadow: 0 4px 20px rgba(0,0,0,0.15);
}

.popup h3 {
  margin: 0 0 10px 0;
  color: #2d3436;
}

.popup p {
  margin: 0 0 15px 0;
  color: #636e72;
}

.popup button {
  padding: 8px 16px;
  background: #0984e3;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  transition: background 0.2s;
}

.popup button:hover {
  background: #74b9ff;
}
</style>
